.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  z-index: 1;
  box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
}
.product:hover{
  transform: scale(1.01);
}
.product__rating{
  display: flex;
}
.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
    background: #f56f2e;;
    border: none ;
    margin-top: 10px;
    font-weight: 600;
    color: white;
    border-radius: 999px;
    cursor: pointer;
}

.product__price{
    margin-top: 5px;
}

.product__info{
    height: 100px;
    margin-bottom: 15px;
}